import React, { useState } from 'react'
import TradingThree from './cycle/three'
import './tradingMining.less'
export default function Index({ lang }) {
  const [curTab, setCurTab] = useState('active') 
  const switchTab = async (current) => {
    setCurTab(current)
  }
  return (
    <div className='trading-mining'>
      <div className='check'>
        {/* <div className={curTab === 'upcoming'  ? 'upcoming now' : 'upcoming'} onClick={() => switchTab('upcoming')}>UPCOMING</div> */}
        {/* <div className={curTab === 'active' ? 'active now' : 'active'} onClick={() => switchTab('active')}>ACTIVE</div> */}
        <div className={curTab === 'upcoming'  ? 'upcoming now' : 'upcoming'}>
          <a href='https://app.deri.finance/#/trade-to-earn/'>UPCOMING</a> 
        </div> 
        <div className='active now'>
          ACTIVE
        </div>
        <div className='finished'>
          <a href='https://app.deri.finance/#/trade-to-earn/finished' rel='noreferrer'>FINISHED</a>
        </div>
      </div>
      {curTab === 'active' && <div className='now-prom'>
        <TradingThree lang={lang} />
      </div>}
      {curTab === 'upcoming' && <div className='now-prom'>
        
      </div>}
    </div>
  )
}